@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

.app {
  background-color: #111;
}

h2 {
  padding-left: 20px;
  font-size: 1.2rem;
}

.row {
  margin-top: 2vh;
  margin-left: 20px;
  color: #fff;
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  cursor: pointer;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  margin-right: 10px;
  transition: -webkit-transform 450ms;
  transition: transform 450ms;
  transition: transform 450ms, -webkit-transform 450ms;
  /* border-radius: 1px; */
}

.row__poster:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  -webkit-transform: scale(1.09);
          transform: scale(1.09);
}

.banner {
  color: #fff;
  object-fit: contain;
  height: 510px;
}

.banner__contents {
  background: linear-gradient(
    77deg,
    rgba(0, 0, 0, 0.6) 0,
    rgba(0, 0, 0, 0) 85%
  );
  /* margin-left: 40px; */
  padding-top: 140px;
  height: 370px;
}

.banner__title {
  font-family: "Bebas Neue", cursive;
  letter-spacing: 0.16rem;
  font-size: 4rem;
  font-weight: 800;
  padding-top: 5rem;
  padding-bottom: 0.5rem;
  margin-left: 40px;
  transition: color 1s cubic-bezier(0.165, 0.84, 0.44, 1);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.45);
}

.banner__description {
  width: 45rem;
  line-height: 1.5;
  padding-top: 1.5rem;
  font-size: 1rem;
  max-width: 560px;
  font-weight: 400;
  margin-left: 40px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.45);
}

.banner__buttons {
  margin-left: 40px;
}

.banner__button {
  font-size: 1rem;
  cursor: pointer;
  /* color: #fff; */
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 0.8rem 2rem 0.8rem 2rem;
  margin-right: 1rem;
  /* background-color: rgba(51, 51, 51, 0.5); */
  background-color: white;
  color: #000;
}

.banner__button:hover {
  /* background-color: #e6e6e6; */
  background-color: rgba(255, 255, 255, 0.75);

  transition: all 0.2s;
}

.second__button {
  background-color: rgba(109, 109, 110, 0.7);
  color: white;
}

.second__button:hover {
  background-color: rgba(109, 109, 110, 0.4);
}

.banner--fadeBottom {
  height: 3.7rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(27, 27, 27, 0.31),
    #111
  );
}

@media only screen and (max-width: 760px) {
  /* For tablets: */
  .banner__description {
    display: none;
  }
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 40px;
  z-index: 1;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-image: linear-gradient(
    360deg,
    transparent,
    rgba(7, 7, 7, 0.51),
    #111
  );

  /* Animations */
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__black {
  background-color: #111;
}

.nav__left {
  margin-left: 10vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__logo {
  position: fixed;
  object-fit: contain;
  left: 40px;
  width: 110px;
}

.nav__list {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  font-size: 1rem;
}

.nav__button {
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.nav__button:hover {
  color: #ddd;
}

.button__active {
  font-weight: bolder;
  cursor: default;
}

.button__active:hover {
  color: #fff;
}

.nav__avatar {
  cursor: pointer;
  position: fixed;
  right: 40px;
  width: 30px;
  object-fit: contain;
  border-radius: 4px;
}

@media only screen and (max-width: 760px) {
  /* For tablets: */
  .nav__list {
    display: none;
  }
}

